import React, { useState } from 'react';
import LoginDiv from '../../Style/UserCSS';
import firebase from '../../firebase';
function Register() {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [passwordConfirm, setPasswordConfirm] = useState('');
  //firebase 를 사용 하는 동안은 app이 멈춰있어야 하기 떄문에
  //async await 을 사용 합니다
  const RegisterHandler = async e => {
    e.preventDefault();
    if (!(name && email && password && passwordConfirm)) {
      return alert('모든 사항을 입력하시오');
    }
    if (password !== passwordConfirm) {
      return alert('비밀번호와 비밀번호 확인 값은 같아야 합니다');
    }
    let createdUser = await firebase
      .auth()
      .createUserWithEmailAndPassword(email, password);
    await createdUser.user.updateProfile({ displayName: name });
    console.log(createdUser.user);
  };

  return (
    <LoginDiv>
      <form>
        <label htmlFor=""> 이름</label>
        <input
          text="name"
          value={name}
          onChange={e => setName(e.target.value)}
        />
        <label htmlFor=""> 이메일</label>
        <input
          text="email"
          value={email}
          onChange={e => setEmail(e.target.value)}
        />
        <label htmlFor=""> 비밀번호</label>
        <input
          type="password"
          text="password"
          value={password}
          onChange={e => setPassword(e.target.value)}
        />
        <label htmlFor=""> 비밀번호 확인</label>
        <input
          type="password"
          text="password"
          value={passwordConfirm}
          onChange={e => setPasswordConfirm(e.target.value)}
        />
        <button type="submit" onChange={RegisterHandler}>
          회원가입
        </button>
      </form>
    </LoginDiv>
  );
}

export default Register;
